.quotes-carousel {
  position: relative;
  background-color: $neutral-1;
  margin: 0;
  height: 480px;
  overflow-y: hidden;
  &:focus {
    box-shadow: none;
  }
  .wrapper {
    position: relative;
    height: 100%;
  }
}

.quote {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: $color-text;
  margin: 0 5%;
  width: 90%;
  height: 100%;
  padding-bottom: 1rem;

  .quote-text {
    margin-bottom: $spacing-unit;
    display: block;
  }

  .quote-author, .quote-source {
    display: block;
  }

  .quote-author {
    @extend %font-sans-extended;
    text-transform: uppercase;
    font-size: 0.8em;
    font-weight: 600;
    letter-spacing: 0.2em;
  }

  .quote-source {
    margin-top: 0.2em;
    font-style: italic;
  }

  p {
    max-width: 700px;
    font-size: 1.4em;
    &:first-of-type:before {
      content: '“';
    }
    &:last-of-type:after {
      content: '”';
    }
  }
}

// HACK: Fade instead of slide
// Pending official Flickity support for fades:
// https://github.com/metafizzy/flickity/issues/26

.flickity-slider {
  transform: none !important;
}

.quote {
  left: 0 !important;
  opacity: 0;
  transition: opacity 1s ease;
  z-index: 1;
  &.is-selected {
    opacity: 1;
    transition: opacity 1s ease;
    z-index: 2;
  }
}

.flickity-prev-next-button {
  z-index: 3;
}
